<template>
    <v-container>
      <v-row no-gutters >
        <v-card class="mx-auto" width="800">
          <v-img
            class="align-end text-white"
            height="200"
            src="https://news-data.pts.org.tw/media/125928/cover.jpg"
            cover
          >
          
            <v-card-title class="d-flex align-center justify-space-between">北科魚池
              
              <v-btn   color="green" icon="mdi-refresh" size="small" @click="RefreshDatas"></v-btn>
            </v-card-title>
            
            
          </v-img> 
            <v-card-subtitle class="pt-1"> 紀錄時間: </v-card-subtitle>
          <v-card-text>
            <div>
            <v-row  no-gutters>
              <v-col v-for="n in links" :key="n" :cols="cols"  class=" d-flex align-center justify-center">
              <v-btn class="ma-2 " :color="n.color" :icon="n.icon " route to = "/Fishdatas" ></v-btn>
              </v-col>
            </v-row>
            <v-row  no-gutters>
              <v-col v-for="n in links" :key="n" :cols="cols"  class=" d-flex align-center justify-center">
                <v-sheet class=" ">{{ n.textname }} </v-sheet> 
              </v-col>
            </v-row>
            <v-row  no-gutters>
              <v-col v-for="n in links" :key="n" :cols="cols"  class=" d-flex align-center justify-center">
                <v-sheet class="ma-2 text-h4">{{n.text}}</v-sheet> 
              </v-col>
            </v-row>
          </div>
          </v-card-text>
        </v-card>
      </v-row>
    </v-container>
  </template>
  
  <script>
    export default {
      data() {
        return {
          FishId:[],
          links: [
            { icon: 'mdi-fishbowl', text: '5', color: 'indigo-darken-1',textname:"游動中" },
            {
              icon: 'mdi-battery-charging-10',
              text: '0',
              color: 'orange',textname:"需充電" 
            },
            { icon: 'mdi-alert', text: '0', color: 'red-darken-1' ,textname:"有錯誤" },
            { icon: 'mdi-wrench', text: '0', color: 'black' ,textname:"維修中" },
          ],
          computed: {
      
            cols() {
              const { lg, sm } = this.$vuetify.display
              return lg ? 3 : sm ? 3 :3      },
    },
        }
      },
      methods:{
        RefreshDatas(){
          const fishData = localStorage.getItem("fish");
          if (fishData) {
            const parsedFishData = JSON.parse(fishData);
            this.FishId = parsedFishData
            
          } else {
             alert("無資料")
}

        }
      }
    }
  </script>